<template>
  <div class="auth-mangement">
    <div class="search-box">
      <gs-button
        type="primary"
        @click="openAddAuthModal"
      >添加方案</gs-button>
      <gs-search
        v-model="keyword"
        placeholder="请输入方案名称"
        @clear="fetchAuthList(1)"
        @search="fetchAuthList"
      />
    </div>
    <data-table
      :loading="loading"
      :table-props="{
        data: tableData
      }"
      :pagination-props="pagination"
      @page-change="pageChange"
      @page-size-change="sizeChange"
    >
      <el-table-column
        prop="privilegeName"
        label="方案名称"
      />
      <el-table-column
        prop="description"
        label="描述"
      />
      <el-table-column
        prop="roles"
        label="授权角色"
      >
        <template slot-scope="scope">
          <gs-tag
            v-for="tag in scope.row.role || []"
            :key="tag"
          >
            {{ tag }}
          </gs-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="100"
        fixed="right"
      >
        <template slot-scope="scope">
          <gs-button
            :disabled="!(!!scope.row.edit)"
            type="text-primary"
            @click="openEditAuthModal(scope)"
          >编辑</gs-button>
          <gs-button
            :disabled="!(!!scope.row.delete)"
            type="text-primary"
            @click="deleteAuth(scope)"
          >删除</gs-button>
        </template>
      </el-table-column>
    </data-table>
    <add-auth
      :privilege-id="editPrivilegeId"
      v-model="editModalVisible"
      @addPrivilege="addPrivilege"
      @editPrivilege="editPrivilege"
    />
  </div>
</template>
<script>
import './style';
import * as service from 'src/service/auth/auth';
import addAuth from './add-auth';
import dataTable from 'src/components/data-table';
export default {
  components: {
    addAuth,
    dataTable
  },
  data() {
    return {
      keyword: '',
      tableData: [
        {
          userId: '1',
          username: '1',
          account: 'in',
          roles: ['amet irure', '222']
        }
      ],
      pagination: {
        total: 0,
        pageSize: 10,
        pageSizes: [10, 20, 30],
        current: 1
      },
      loading: false,
      editPrivilegeId: '',
      editModalVisible: false
    };
  },
  computed: {

  },

  created() {
    this.fetchAuthList(1);
  },

  methods: {
    fetchAuthList(isfirstPage = 0) {
      if (isfirstPage === 1) this.pagination.current = 1;
      this.loading = true;
      service.getPrivilegeList({
        offset: (this.pagination.current - 1) * this.pagination.pageSize,
        limit: this.pagination.pageSize,
        query: this.keyword
      }).then(data => {
        data.lists.map(item => {
          item.role = item.roles.map(i => i.roleName);
          return item;
        });
        this.tableData = data.lists;
        this.pagination.total = data.sum;
        this.loading = false;
      }).catch(e => {
        this.loading = false;
      });
    },
    pageChange(current) {
      this.pagination.current = current;
      this.fetchAuthList();
    },
    sizeChange(size) {
      this.pagination.pageSize = size;
      this.fetchAuthList(1);
    },
    addPrivilege(form) {
      service.createPrivilege(form).then(data => {
        this.fetchAuthList(1);
        this.$Message.success('添加方案成功！');
        this.editModalVisible = false;
      });
    },
    editPrivilege({id, params}) {
      service.editPrivilege(id, params).then(data => {
        this.fetchAuthList();
        this.$Message.success('修改方案成功！');
        this.editModalVisible = false;
      });
    },
    openEditAuthModal({row}) {
      this.editPrivilegeId = row.privilegeId;
      this.editModalVisible = true;
    },
    deleteAuth({row}) {
      if (row.privilegeId && (!row.roles || (row.roles && !row.roles.length))) {
        this.$Modal.confirm({
          title: '警告',
          content: '是否删除该权限方案？删除后将无法恢复',
          onOk: () => {
            service.deletePrivilege(row.privilegeId).then(data => {
              this.$Message.success('删除权限方案成功');
              this.fetchAuthList();
            });
          }
        });
      } else {
        this.$Message.warning('请先全清空使用该权限方案的角色！');
      }
    },
    openAddAuthModal() {
      this.editPrivilegeId = '';
      this.editModalVisible = true;
    }
  }
};
</script>



