<template>
  <gs-modal
    :title="title"
    v-model="visible"
    class="add-auth"
    top="60px"
    @cancel="$emit('input', false)"
    @confirm="confirm"
    @visible-change="visibleChange"
  >
    <gs-loading
      :is-show="loading"
    />
    <gs-form
      ref="form"
      :model="form"
      :rules="rules"
      label-width="80px"
    >
      <gs-form-item
        label="方案名称"
        prop="privilegeName"
      >
        <gs-input v-model="form.privilegeName" />
      </gs-form-item>
      <gs-form-item
        label="描述"
        prop="description"
      >
        <gs-textarea v-model="form.description" />
      </gs-form-item>
      <gs-form-item
        label="授权内容"
        prop="module"
      >
        <div class="modules">
          <div class="module">
            <label class="title">授权模块</label>
            <gs-checkbox-group
              v-model="form.modules"
              @change="authModuleCheckboxChange"
            >
              <gs-checkbox
                v-for="item in authModules"
                :label="item.moduleId"
                :key="item.moduleId"
              >{{ item.moduleName }}</gs-checkbox>
            </gs-checkbox-group>
          </div>
          <div class="bussiness-obj-ope">
            <label class="title">管理权限</label>
            <gs-checkbox-group
              v-model="form.modules"
              @change="opeModuleCheckboxChange"
            >
              <gs-checkbox
                v-for="item in opeModules"
                :label="item.moduleId"
                :key="item.moduleId"
              >{{ item.moduleName }}</gs-checkbox>
            </gs-checkbox-group>
          </div>
        </div>
      </gs-form-item>
      <gs-form-item
        label="授权角色"
        prop="roles"
      >
        <gs-select
          v-model="form.roles"
          multiple
        >
          <gs-option
            v-for="item in roleOptions"
            :key="item.roleId"
            :label="item.roleName"
            :title="item.roleName"
            :value="item.roleId"
          />
        </gs-select>
      </gs-form-item>
    </gs-form>
  </gs-modal>
</template>
<script>
import * as service from 'src/service/auth/auth';
import {MODULE} from 'src/utils/index.js';
const {
  techData,
  businessObject,
  manageTechCagatory,
  editTechInfo,
  manageBusinessCagatory,
  editBusinessInfo
} = MODULE;
export default {
  props: {
    privilegeId: {
      type: String,
      default: ''
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      roleOptions: [],
      form: {
        modules: [],
        description: '',
        privilegeName: '',
        roles: []
      },
      loading: false,
      rules: {
        privilegeName: [{required: true, message: '请填写方案名称', trigger: 'blur'}]
      },
      opeModules: [],
      authModules: []
    };
  },
  computed: {
    visible: {
      get() {
        return this.value;
      },
      set(val) {
        return val;
      }
    },
    title() {
      return this.privilegeId ? '编辑方案' : '添加方案';
    }
  },
  methods: {
    confirm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.privilegeId) {
            this.$emit('editPrivilege', {
              id: this.privilegeId,
              params: this.form
            });
          } else {
            this.$emit('addPrivilege', this.form);
          }
        }
      });
    },
    visibleChange(val) {
      if (val) {
        this.loading = true;
        this.$refs.form.clearValidate();
        const getModules = () => {
          return service.getModules().then(data => {
            this.opeModules = data.filter(i => i.type === 0);
            this.authModules = data.filter(i => i.type === 1);
          });
        };
        const getRole = () => {
          return service.getRoleList({limit: -1, query: ''}).then(data => {
            this.roleOptions = data.lists;
          });
        };
        this.form = {
          modules: [],
          description: '',
          privilegeName: '',
          roles: []
        };
        Promise.all([getModules(), getRole()]).then(_ => {
          if (this.privilegeId) {
            this.getPrivilegeDetail().then(_ => {
              this.loading = false;
            });
          } else {
            this.loading = false;
          }
        });
      }
    },
    getPrivilegeDetail() {
      return service.getPrivilegeDetail(this.privilegeId).then(data => {
        const {description, privilegeName, modules, roles} = data;
        this.form = {
          description,
          privilegeName,
          modules: modules.map(i => i.moduleId),
          roles: roles.map(i => i.roleId)
        };
      });
    },
    opeModuleCheckboxChange(val) {
      if (Array.isArray(val)) {
        this.form.modules = val.includes(editTechInfo)
          ? Array.from(new Set([techData, editTechInfo, ...this.form.modules]))
          : this.form.modules;
        this.form.modules = val.includes(manageTechCagatory)
          ? Array.from(new Set([techData, manageTechCagatory, ...this.form.modules]))
          : this.form.modules;
        this.form.modules = val.includes(editBusinessInfo)
          ? Array.from(new Set([businessObject, editBusinessInfo, ...this.form.modules]))
          : this.form.modules;
        this.form.modules = val.includes(manageBusinessCagatory)
          ? Array.from(new Set([businessObject, manageBusinessCagatory, ...this.form.modules]))
          : this.form.modules;
      }
    },
    authModuleCheckboxChange(val) {
      if (!val.includes(techData)) {
        const removeManage = this.form.modules.findIndex(item => item === editTechInfo);
        if (removeManage !== -1) this.form.modules.splice(removeManage, 1);
        const removeEdit = this.form.modules.findIndex(item => item === manageTechCagatory);
        if (removeEdit !== -1) this.form.modules.splice(removeEdit, 1);
      }
      if (!val.includes(2)) {
        const removeManage = this.form.modules.findIndex(item => item === editBusinessInfo);
        if (removeManage !== -1) this.form.modules.splice(removeManage, 1);
        const removeEdit = this.form.modules.findIndex(item => item === manageBusinessCagatory);
        if (removeEdit !== -1) this.form.modules.splice(removeEdit, 1);
      }
    }
  }
};
</script>




